.registro-form-containerRegistro {
  display: flex;
  max-width: 800px;
  height: auto;
  margin: auto;
  margin-bottom: 4%;
  padding: 0px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
}

.registro-image-containerRegistro {
  flex: 1;
  text-align: center;
}

.registro-imageRegistro {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0px;
  
}

.registro-formRegistro {
  margin-left: 35px;
  margin-right: 35px;
  flex: 1;
  padding-left: 10px;
  display: flex;
  flex-direction: column;

}
.formulario{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.RegistroLabel{
  font-family: 'Roboto Condensed', sans-serif;
  margin-bottom: 5px;
  margin-top: 5px;
}

.input-error2 {
  border: 1px solid red;
  
}

@media (max-width: 768px) {
  .formulario {
    grid-template-columns: 1fr; /* Solo una columna en pantallas pequeñas */
  }

  .registro-image-containerRegistro{
    display: none;
  }
}

  
